/* src/main/frontend/src/pages/App.css */

.App {
  text-align: left;
  margin-left: 250px; /* 기본 여백 */
  padding: 20px;
  transition: margin-left 0.3s;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.navbar.open + .App {
  margin-left: 60px; /* 네비게이션 바가 축소되었을 때 여백 줄이기 */
}

@media screen and (max-width: 768px) {
  .App {
    margin-left: 60px; /* 작은 화면에서는 네비게이션 바 크기에 맞게 여백 조정 */
  }

  .navbar.open + .App {
    margin-left: 250px; /* 작은 화면에서 네비게이션 바가 열렸을 때 여백 조정 */
  }
}