/* src/main/frontend/src/components/NavBar.css */

.navbar {
    width: 250px;
    background-color: #282c34;
    color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    position: fixed;
    top: 0;
    left: 0;
    transition: width 0.3s; /* 너비 변경 애니메이션 추가 */
}

.navbar.open {
    width: 60px; /* 아이콘만 보이게 조정 */
}

.navbar-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
}

.navbar-toggle {
    display: none;
    cursor: pointer;
    font-size: 30px;
}

.user-info {
    display: none; /* 햄버거 메뉴가 열리지 않은 경우 유저 정보 숨기기 */
}

.navbar-list {
    list-style-type: none;
    padding: 0;
    width: 100%;
    margin: 0;
    overflow: hidden;
}

.navbar-item {
    width: 100%;
    margin-bottom: 15px; /* 링크 간의 간격 설정 */
}

.navbar-link {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    color: white;
    text-decoration: none;
    transition: background-color 0.2s;
    font-size: 24px; /* 폰트 크기 설정 */
}

.navbar-link:hover {
    background-color: #9370db;
    box-shadow: 0 4px 8px rgba(106, 77, 171, 0.8); /* 더 진한 그림자 색상 추가 */
}

.navbar-icon {
    margin-right: 10px;
    font-size: 30px; /* 아이콘 크기 설정 */
}

.navbar-text {
    display: inline;
}

@media screen and (max-width: 768px) {
    .navbar {
        width: 60px; /* 작은 화면에서는 아이콘만 보이게 */
        height: 100vh;
        padding-top: 10px;
    }

    .navbar.open {
        width: 250px; /* 메뉴가 열리면 전체 표시 */
    }

    .navbar-toggle {
        display: block;
    }

    .navbar-list {
        display: none; /* 기본적으로 메뉴는 숨김 */
    }

    .navbar-list.open {
        display: block; /* isOpen이 true일 때만 메뉴가 보임 */
    }

    .navbar-text {
        display: none; /* 작은 화면에서는 텍스트 숨기기 */
    }

    .navbar.open .navbar-text {
        display: inline;
    }

    .navbar.open .user-info {
        display: inline;
    }
}
